import { type VariantProps } from "class-variance-authority";
import { Menu } from "lucide-react";
import { ReactNode } from "react";

import { siteConfig } from "@/config/site";
import { cn } from "@/lib/utils";

import LaunchUI from "../../logos/launch-ui";
import { Button, buttonVariants } from "../../ui/button";
import {
  Navbar as NavbarComponent,
  NavbarLeft,
  NavbarRight,
} from "../../ui/navbar";
import Navigation from "../../ui/navigation";
import { Sheet, SheetContent, SheetTrigger } from "../../ui/sheet";

// ðŸ‘‰ Clerk imports
import { SignInButton, SignUpButton } from "@clerk/nextjs";

interface NavbarLink {
  text: string;
  href: string;
}

interface NavbarActionProps {
  text: string;
  href: string;
  variant?: VariantProps<typeof buttonVariants>["variant"];
  icon?: ReactNode;
  iconRight?: ReactNode;
  isButton?: boolean;
}

interface NavbarProps {
  logo?: ReactNode;
  name?: string;
  homeUrl?: string;
  mobileLinks?: NavbarLink[];
  actions?: NavbarActionProps[];
  showNavigation?: boolean;
  customNavigation?: ReactNode;
  className?: string;
}

export default function Navbar({
  logo = <LaunchUI />,
  name = "Warrantiya",
  homeUrl = siteConfig.url,
  mobileLinks = [
    { text: "Our Services", href: siteConfig.url },
    { text: "About Us", href: siteConfig.url },
    { text: "Pricing", href: siteConfig.url },
  ],
  actions = [
    { text: "Sign in", href: siteConfig.url, isButton: false },
    {
      text: "Get Started",
      href: siteConfig.url,
      isButton: true,
      variant: "default",
    },
  ],
  showNavigation = true,
  customNavigation,
  className,
}: NavbarProps) {
  return (
    <header className={cn("sticky top-0 z-50 -mb-4 px-4 pb-4", className)}>
      <div className="fade-bottom bg-background/15 absolute left-0 h-24 w-full backdrop-blur-lg"></div>
      <div className="max-w-container relative mx-auto">
        <NavbarComponent>
          <NavbarLeft>
            <a
              href={homeUrl}
              className="flex items-center gap-2 text-xl font-bold"
            >
              {logo}
              {name}
            </a>
            {showNavigation && (customNavigation || <Navigation />)}
          </NavbarLeft>

          <NavbarRight>
            {actions.map((action, index) => {
              const textLower = action.text.toLowerCase();

              // ðŸ‘‰ SIGN IN BUTTON â†’ Clerk Sign-In modal
              if (textLower === "sign in") {
                return (
                  <SignInButton key={index} mode="modal">
                    <Button
                      variant="ghost"
                      asChild
                      className="hidden md:block"
                    >
                      <a>{action.text}</a>
                    </Button>
                  </SignInButton>
                );
              }

              // ðŸ‘‰ GET STARTED / JOIN WAITLIST â†’ Clerk Sign-Up modal
              if (
                textLower === "get started" ||
                textLower === "join waitlist"
              ) {
                return (
                  <SignUpButton
                    key={index}
                    mode="modal"
                    redirectUrl="/dashboard" // change if needed
                  >
                    <Button
                      variant={action.variant || "default"}
                      asChild
                    >
                      <a>
                        {action.icon}
                        {action.text}
                        {action.iconRight}
                      </a>
                    </Button>
                  </SignUpButton>
                );
              }

              // ðŸ‘‰ Fallback for normal non-auth buttons
              return action.isButton ? (
                <Button
                  key={index}
                  variant={action.variant || "default"}
                  asChild
                >
                  <a href={action.href}>
                    {action.icon}
                    {action.text}
                    {action.iconRight}
                  </a>
                </Button>
              ) : (
                <a
                  key={index}
                  href={action.href}
                  className="hidden text-sm md:block"
                >
                  {action.text}
                </a>
              );
            })}

            {/* MOBILE MENU */}
            <Sheet>
              <SheetTrigger asChild>
                <Button
                  variant="ghost"
                  size="icon"
                  className="shrink-0 md:hidden"
                >
                  <Menu className="size-5" />
                  <span className="sr-only">Toggle navigation menu</span>
                </Button>
              </SheetTrigger>
              <SheetContent side="right">
                <nav className="grid gap-6 text-lg font-medium">
                  <a
                    href={homeUrl}
                    className="flex items-center gap-2 text-xl font-bold"
                  >
                    <span>{name}</span>
                  </a>

                  {mobileLinks.map((link, index) => (
                    <a
                      key={index}
                      href={link.href}
                      className="text-muted-foreground hover:text-foreground"
                    >
                      {link.text}
                    </a>
                  ))}

                  {/* MOBILE SIGN IN / SIGN UP */}
                  <SignInButton mode="modal">
                    <a className="hover:text-foreground">Sign in</a>
                  </SignInButton>

                  <SignUpButton mode="modal" redirectUrl="/dashboard">
                    <a className="hover:text-foreground">Get Started</a>
                  </SignUpButton>
                </nav>
              </SheetContent>
            </Sheet>
          </NavbarRight>
        </NavbarComponent>
      </div>
    </header>
  );
}
